import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import { TagInput, SingleInput } from './inputs';
// Validation Schema
const CandidateSearchSchema = Yup.object({
  Technologies: Yup.array().of(Yup.string()).min(1, 'At least one technology is required'),
  Companies: Yup.array().of(Yup.string()),
  domains: Yup.array().of(Yup.string()),
  preferredLocations: Yup.array().of(Yup.string()),
  techRole: Yup.string().required('Tech Role is required'),
  CurrentLocation: Yup.string().required('Current Location is required'),
  qualification: Yup.string().required('Qualification is required'),
  experience: Yup.string().required('Experience is required'),
  salary: Yup.string().required('Salary is required'),
  noticePeriod: Yup.string().required('Notice Period is required'),
});



export default function SearchCandidates() {
  const formik = useFormik({
    initialValues: {
      Technologies: ['Java'],
      Companies: ['Google'],
      domains: ['IT'],
      preferredLocations: ['Mumbai'],
      techRole: 'Java Developer',
      CurrentLocation: 'Mumbai',
      qualification: 'Graduate',
      experience: '2 years',
      salary: '50000',
      noticePeriod: '1 month',
    },
    validationSchema: CandidateSearchSchema,

    onSubmit: async (values) => {
      console.log('Form submitted with values:', values);
      // Implement your search logic here
    },
  });

  // Helper function to handle adding tags immutably

  const handleAddTag = (field: keyof typeof formik.values, tag: string) => {
    formik.setFieldValue(field, [...formik.values[field], tag]);
  };
  
  const handleRemoveTag = (field: keyof typeof formik.values, tag: string) => {
    formik.setFieldValue(
      field,
      (formik.values[field] as string[]).filter((item: string) => item !== tag),
    );
  };
  

  return (
    <section className="p-4 space-y-4">
      <form onSubmit={formik.handleSubmit}>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Technologies */}
          <TagInput
            title="Technologies"
            placeholder="Enter Single or Multiple Technologies"
            tags={formik.values.Technologies}
            onAddTag={(tag) => handleAddTag('Technologies', tag)}
            onRemoveTag={(tag) => handleRemoveTag('Technologies', tag)}
            showMandatoryButton
          />

          {/* Companies */}
          <TagInput
            title="Companies"
            placeholder="Enter Single or Multiple Previous Companies"
            tags={formik.values.Companies}
            onAddTag={(tag) => handleAddTag('Companies', tag)}
            onRemoveTag={(tag) => handleRemoveTag('Companies', tag)}
          />

          {/* Tech Role */}
          <SingleInput
            title="Tech Role"
            placeholder="Enter Tech Role"
            value={formik.values.techRole}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />

          {/* Current Location */}
          <SingleInput
            title="Current Location"
            placeholder="Enter Current Location"
            value={formik.values.CurrentLocation}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />

          {/* Domain */}
          <TagInput
            title="Domain"
            placeholder="Enter Single or Multiple Domains"
            tags={formik.values.domains}
            onAddTag={(tag) => handleAddTag('domains', tag)}
            onRemoveTag={(tag) => handleRemoveTag('domains', tag)}
          />

          {/* Preferred Location */}
          <TagInput
            title="Preferred Location"
            placeholder="Enter Single or Multiple Locations"
            tags={formik.values.preferredLocations}
            onAddTag={(tag) => handleAddTag('preferredLocations', tag)}
            onRemoveTag={(tag) => handleRemoveTag('preferredLocations', tag)}
          />

          {/* Qualification */}
          <SingleInput
            title="Qualification"
            placeholder="Enter Qualification Details"
            value={formik.values.qualification}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />

          {/* Experience */}
          <SingleInput
            title="Experience"
            placeholder="Enter Minimum Experience"
            value={formik.values.experience}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />

          {/* Salary */}
          <SingleInput
            title="Salary"
            placeholder="Enter Minimum Salary"
            value={formik.values.salary}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />

          {/* Notice Period */}
          <SingleInput
            title="Notice Period"
            placeholder="Enter Minimum Days"
            value={formik.values.noticePeriod}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
        </div>

        {/* Submit Button */}
        <div className="flex justify-end mt-6">
          <button
            type="submit"
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
            disabled={!formik.isValid || formik.isSubmitting}
          >
            {formik.isSubmitting ? 'Searching...' : 'Search Candidates'}
          </button>
        </div>
      </form>
    </section>
  );
}